.projectCard {
  width: 300px;
  height: 250px;
  border: 1px solid black;
  text-align: center;
  vertical-align: middle;
  line-height: 250px;
  background-color: var(--accentTwo);
  border-radius: 10px;
  transition: 0.25s;
}

.projectCard:hover {
  background-color: var(--accentOne);
}
